import {resume} from './resume.js'
;(function () {
  // 封装DOM选择方法
  /**
   *
   * @param {string} selector
   * @returns {Element}
   */
  function $(selector) {
    return document.querySelector(selector)
  }
  /**
   *
   * @param {string} selector
   * @returns {NodeList}
   */
  function $$(selector) {
    return document.querySelectorAll(selector)
  }
  // 获取需要操作的DOM元素
  var doms = {
    resumePersonalProfile: $('.resume-personal-profile'),
    resumeEduExperienceContent: $('.resume-edu-experience .resume-content'),
    resumeWorkExperienceContent: $('.resume-work-experience .resume-content'),
    resumeProfesstionalSkillsContainer: $('.resume-professional-skills-container'),
    resumeProjectExperienceContainer: $('.resume-project-experience-container'),
    resumeSelfEvaluationContent: $('.resume-self-evaluation-content')
  }
  // 定义初始化函数
  const init = function () {
    // 初始化渲染
    render()
  }
  // 定义渲染函数
  function render() {
    // 渲染个人信息
    doms.resumePersonalProfile.innerHTML = `        <!-- 姓名 -->
    <div class="resume-personal-profile-name">
      <h2>${resume.personalProfile.name}</h2>
    </div>
    <div class="resume-personal-profile-basic">
      <!-- 年龄 -->
      <div class="resume-personal-profile-age">
        <i class="iconfont icon-ziyuan48"></i>
        <span>年龄：${resume.personalProfile.age}</span>
      </div>
      <!-- 电话 -->
      <div class="resume-personal-profile-phone">
        <i class="iconfont icon-dianhua1"></i>
        <span>电话：<a href="tel:${resume.personalProfile.phone}">${resume.personalProfile.phone}</a></span>
      </div>
      <!-- 邮箱 -->
      <div class="resume-personal-profile-email">
        <i class="iconfont icon-youxiang"></i>
        <span>邮箱：<a href="mailto:${resume.personalProfile.email}">${resume.personalProfile.email}</a></span>
      </div>
      <!-- 生日 -->
      <div class="resume-personal-profile-birthday">
        <i class="iconfont icon-shengrix"></i>
        <span>生日：${resume.personalProfile.birthday}</span>
      </div>
      <!-- 工作经验 -->
      <div class="resume-personal-profile-years">
        <i class="iconfont icon-gongzuojingyan"></i>
        <span>工作经验：${resume.personalProfile.workYears}</span>
      </div>
      <!-- 学历 -->
      <div class="resume-personal-profile-edu-degree">
        <i class="iconfont icon-edu-line"></i>
        <span>学历：${resume.personalProfile.eduDegree}</span>
      </div>
    </div>
    <!-- 照片 -->
    <div class="resume-personal-profile-photo">
      <img src="${resume.personalProfile.photo}" alt="" />
    </div>`
    // 渲染教育经历
    let eduExperience = ''
    for (let i = 0; i < resume.eduExperience.length; i++) {
      eduExperience += `          <div class="resume-edu-experience-item">
      <i class="iconfont icon-dian"></i>
      <div class="resume-time">${resume.eduExperience[i].time}</div>
      <div class="resume-edu-experience-university">${resume.eduExperience[i].university}</div>
      <div class="resume-edu-experience-major">${resume.eduExperience[i].major}</div>
    </div>`
    }
    doms.resumeEduExperienceContent.innerHTML = eduExperience
    // 渲染工作经历
    let workExperience = ''
    for (let i = 0; i < resume.workExperience.length; i++) {
      workExperience += `          <div class="resume-work-experience-top">
      <i class="iconfont icon-dian"></i>
      <div class="resume-time">${resume.workExperience[i].time}</div>
      <div class="resume-work-experience-company">${resume.workExperience[i].company}</div>
      <div class="resume-work-experience-job">${resume.workExperience[i].job}</div>
    </div>
    <div class="resume-work-experience-department">${resume.workExperience[i].department}</div>
    <p class="resume-work-experience-jobDescription">${resume.workExperience[i].jobDescription}</p>`
    }
    doms.resumeWorkExperienceContent.innerHTML = workExperience
    // 渲染专业技能
    let professionalSkills = ''
    for (let i = 0; i < resume.professionalSkills.length; i++) {
      professionalSkills += `<div class="resume-professional-skills-item">${resume.professionalSkills[i]}</div>`
    }
    doms.resumeProfesstionalSkillsContainer.innerHTML = professionalSkills
    // 渲染项目经验
    let projectExperience = ''
    for (let i = 0; i < resume.projectExperience.length; i++) {
      let projectExperienceRows = ''
      for (let j = 0; j < resume.projectExperience[i].detail.length; j++) {
        projectExperienceRows += `<div class="resume-project-experience-detail-item">${resume.projectExperience[i].detail[j].title}</div><div class="resume-project-experience-detail-item">${resume.projectExperience[i].detail[j].content}</div>`
      }
      projectExperience += `          <div class="resume-project-experience-name">${resume.projectExperience[i].name}</div>
      <div class="resume-project-experience-detail">
        ${projectExperienceRows}
      </div>`
    }
    doms.resumeProjectExperienceContainer.innerHTML = projectExperience
    // 渲染自我评价
    doms.resumeSelfEvaluationContent.innerHTML = resume.selfEvaluation
  }
  init()
})()
